import { createStore } from 'redux';

const defaultState = {
  done:   {},
  filter: [],
  sort:   [],
  edit:   {},
  expand: {}
};
const data = localStorage.getItem('itgs');
if ( data ) try { Object.assign(defaultState,JSON.parse(data)); } catch (e) {}

defaultState.filter = defaultState.filter.filter( fn => fn != null )

export const hideDisabled = ()=> ({done})=> done > 0;
export const showSearch   = (state)=> ({desc})=> desc.match(state.search);

const toggleFilter = ( state, fn ) => ({ ...state, filter:(
  state.filter.includes(fn)
  ? state.filter.filter( f => f !== fn )
  : [ ...state.filter, fn ]
)});

const replaceFilter = ( state, fn ) => ({ ...state, filter:[
  ...(state.filter.includes(fn)
  ? state.filter.filter( f => f !== fn )
  : state.filter), fn
]});

const reducer = ( state=defaultState, action )=> {
  switch(action.type){
    case "edit:toggle": return   { ...state, edit:{ ...state.edit, [action.key]: ! state.edit[action.key] } }
    case "expand:toggle": return { ...state, expand:{ ...state.expand, [action.key]: ! state.expand[action.key] } }
    case "edit:done:set": return { ...state, done:{ ...state.done, [action.name]: action.value } }
    case "view:disabled:toggle": return toggleFilter(state,hideDisabled);
    case "drawer:toggle": return { ...state, drawer: ! state.drawer };
    case "search:change": return replaceFilter({ ...state, search:action.value }, showSearch);
    default: return state;
  }
}

export const store = createStore(reducer);