
import React, { useEffect, useReducer, useState } from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import { Model, SubModel } from "./DataModel";
import { Checkbox, IconButton } from '@material-ui/core';
import { Slider } from '@material-ui/core';

import './App.css';
import { useDispatch, useSelector } from 'react-redux';
import Drawer from './Drawer';
import AppBar from './AppBar';
import { ArrowForward, ArrowForwardIos, Edit, ExpandLess, ExpandMore } from '@material-ui/icons';

const SubItem = ({data})=> {
  return (
  <Table>
    <TableHead>
      <TableRow>
        <TableCell>{data[0]}</TableCell>
        <TableCell>{data[1]}</TableCell>
      </TableRow>
    </TableHead>
  </Table> );
}

function App() {
  const state = useSelector( state => state );
  const dispatch = useDispatch();
  const toggle = (name)=> (e,value) => dispatch({type:'edit:done:set',name,value});
  useEffect( $=> localStorage.setItem('itgs',JSON.stringify(state)), [state] )
  let sorted = Model.map( ([order,system,point,desc,area]) => {
    const key = system+point;
    return {order,system,point,desc,area,
      done:state.done[key]||0,
      edit:state.edit[key]||false,
      expand:state.expand[key]||false
    }
  });
  // if ( state.sort )
  state.filter.forEach( fn => sorted = sorted.filter(fn(state)) );
  return (
    <div className="App">
      <AppBar/>
      <Drawer/>
      <TableContainer component={Paper}>
        <Table>
          <TableBody>
            { sorted.map( (data) => {
              const {order,system,point,desc,area,done,edit,expand} = data;
              const key = `${system}${point}`; 
              return (
              <TableRow key={key} className={`cat-slider-${state.done[key]||0}`}>
                <TableCell align="right">{system} {point}</TableCell>
                <TableCell align="left">
                  {edit ?
                  <Slider
                    name={key}
                    value={done}
                    onChange={toggle(key)}
                    step={1}
                    marks
                    min={0}
                    max={4}
                  /> : done }
                </TableCell>
                <TableCell component="th" align="left">{desc}</TableCell>
                <TableCell scope="row" align="right">{area}</TableCell>
                <TableCell align="right">
                  <IconButton edge="start" onClick={e=>dispatch({type:"edit:toggle",key})}
                    ><Edit/></IconButton>
                  <IconButton edge="start" onClick={e=>dispatch({type:"expand:toggle",key})}
                  ><ArrowForwardIos/>
                  </IconButton>
                </TableCell>
              </TableRow>
            )})}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
}

export default App;
